import { getSupabaseClient, Tables } from './supabase';

export async function listGuides(): Promise<any[]> {
  const supabase = getSupabaseClient();
  const { data, error } = await supabase
    .from('guides')
    .select(`
      *,
      user_profiles (
        avatar_url,
        hero_image_url
      )
    `)
    .order('created_at', { ascending: false });
  if (error) throw error;
  return data ?? [];
}

export async function getGuideById(id: string): Promise<any> {
  const supabase = getSupabaseClient();
  const { data, error } = await supabase
    .from('guides')
    .select(`
      *,
      user_profiles (
        avatar_url,
        hero_image_url
      )
    `)
    .eq('id', id)
    .single();
  if (error) throw error;
  return data ?? null;
}

export async function upsertGuide(payload: Partial<Tables['guides']['Row']>): Promise<Tables['guides']['Row']> {
  const supabase = getSupabaseClient();
  const isInsert = !payload.id;

  if (isInsert) {
    // Insert explcito con user_id del usuario actual
    const { data: sessionData } = await supabase.auth.getSession();
    const sessionUserId = sessionData?.session?.user?.id;
    if (!sessionUserId) throw new Error('No hay sesin para crear el gua');

    const insertPayload = {
      name: payload.name ?? '',
      location: payload.location ?? null,
      bio: payload.bio ?? null,
      price_per_day: payload.price_per_day ?? null,
      age: payload.age ?? null,
      languages: payload.languages ?? null,
      specialties: payload.specialties ?? null,
      user_id: sessionUserId,
    } as Partial<Tables['guides']['Row']>;

    console.log('upsertGuide - insertPayload:', insertPayload);

    const { data, error } = await supabase
      .from('guides')
      .insert(insertPayload)
      .select()
      .single();
    if (error) {
      console.error('upsertGuide - insert error:', error);
      throw new Error(error.message || 'No se pudo crear el gua');
    }
    console.log('upsertGuide - insert success:', data);
    return data as Tables['guides']['Row'];
  }

  // Update explcito por id (evita que upsert intente insertar y dispare NOT NULL)
  const updatePayload = {
    name: payload.name,
    location: payload.location,
    bio: payload.bio,
    price_per_day: payload.price_per_day,
    age: payload.age,
    languages: payload.languages,
    specialties: payload.specialties,
  } as Partial<Tables['guides']['Row']>;

  console.log('upsertGuide - updatePayload:', updatePayload);

  const { data, error } = await supabase
    .from('guides')
    .update(updatePayload)
    .eq('id', payload.id as string)
    .select()
    .single();
  if (error) {
    console.error('upsertGuide - update error:', error);
    throw new Error(error.message || 'No se pudo actualizar el gua');
  }
  console.log('upsertGuide - update success:', data);
  return data as Tables['guides']['Row'];
}

export async function deleteGuide(id: string): Promise<void> {
  const supabase = getSupabaseClient();
  const { error } = await supabase
    .from('guides')
    .delete()
    .eq('id', id);
  if (error) throw error;
}

export async function reassignGuideOwner(guideId: string): Promise<void> {
  const supabase = getSupabaseClient();
  const { data: sessionData } = await supabase.auth.getSession();
  const sessionUserId = sessionData?.session?.user?.id;
  if (!sessionUserId) throw new Error('Sin sesin: inicia sesin para reasignar');
  const { error } = await supabase
    .from('guides')
    .update({ user_id: sessionUserId })
    .eq('id', guideId);
  if (error) throw new Error(error.message || 'No se pudo reasignar');
}

